const showLoader = () => {
	const loader = document.querySelector("#loader");
	loader.style.display = "block";
}

const hideLoader = () => {
	const loader = document.querySelector("#loader");
	loader.style.display = "none";
}

document.querySelector(".download").addEventListener("click",()=>{
	let link = document.querySelector(".linkOfVideo").value;
	const data = {
		"url": `${link}`
	};

	showLoader(); // Show loader before sending request
	fetch('https://save-from.net/api/convert', {
	  method: 'POST', // or 'PUT'
	headers: {
		'Content-Type': 'application/json',
	},
	body: JSON.stringify(data),
	})
	.then(response => response.json())
	.then(data => {
		const AllUrls = data.url;
		console.log(data);
		showAllUrls(data.hosting,AllUrls,data.thumb,data.meta.title,data.id);
	})
	.catch(err => {
		hideLoader();
	  showErrorMsg("Please enter valid video url"); //ask the user to enter the url again
	})
	.finally(() => {
	  hideLoader(); // Hide loader after request is completed (whether successful or not)
	});

	//ADD STYLES WITH JavaScript
	//control loader appearance and showing error message
	//DOM elements
	const errorMsg = document.getElementById("error-message"); //error message

	//Error message styles
	const showErrorMsg = (errMessage) => {
	  errorMsg.textContent = errMessage; //error message content
	  errorMsg.style.display = "block"; //show error message
	  errorMsg.addEventListener("animationend", () => errorMsg.style.display = "none"); //hide it again after finishing its animation
	}
})

function showAllUrls(isTwitter,AllUrls,img,title){

	if(isTwitter=="twitter.com"){

		document.querySelector(".LinksDad").innerHTML=`
	
		<img src="${img}" alt="" style="width: 70%; margin: 0 30px auto; border-radius: 20px;">
		<br> <br>
	    <h1 style="text-align: center; color: #000; font-size: 29px; margin: 1em auto;">${title}</h1>
		<br>
		
		`;


		AllUrls.forEach((e) => {

			if(e.type=="mp4"){
	
				let x = parseInt(e.filesize);
				
				document.querySelector(".LinksDad").innerHTML+=`
				<div class="linkUrl">
					<a href="${e.url}" class="Link" id="video" class="Link" target="blank">${e.quality} ${e.type}</a>
				</div>
				`
	
			};
	
		});
	} 	else {
		showErrorMsg("Please enter valid video url"); //ask the user to enter the url again
	}
};

document.getElementById("delete").addEventListener("click", function(){
    document.getElementById("url").value = "";
    document.querySelector(".LinksDad").innerHTML = "";
});